@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  color: #000512;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  width: 375px;
  height: 100%;
  display: flex;
  flex-flow: column;
  position: relative;
  margin: 0 auto;
}

.main {
  flex: 1 0 auto;
  position: relative;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

p {
  margin: 0;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.header {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  color: #50B848;
  padding: 6.5px 19px 12px;
}

.header__bank-logo {
  padding: 5.5px 0 0;
}

.header__bank-logo-svg {
  width: 126px;
  height: 26px;
}

.header__phone-link {
  font-weight: 700px;
  padding: 0 1px 0 0;
}

.preview {
  min-height: 466px;
  text-align: center;
  background: url("../img/preview-background.png") no-repeat 0 67.5%/375px 270px;
  padding: 12px 0 32px;
}
@media (min-resolution: 2dppx) {
  .preview {
    background: url("../img/preview-background@2x.png") no-repeat 0 67.5%/375px 270px;
  }
}

.preview__title {
  width: 80%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
  margin: 0 auto 18px;
}

.preview__discount {
  width: 153px;
  min-height: 33px;
  font-family: "Montserrat", "Arial", sans-serif;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  color: #ffffff;
  background: #50B848;
  border-radius: 4px;
  padding: 9px 0 8px;
  margin: 0 auto 16px;
}

.preview__description {
  width: 247px;
  font-size: 14px;
  line-height: 17px;
  font-weight: 300;
  margin: 0 auto 266px;
}

.preview__pharmacy-container {
  width: 100%;
  min-height: 60px;
  display: flex;
  align-items: center;
  background: #008E62;
}

.preview__pharmacy-container-wrapper {
  width: 78.6%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.preview__social-pharmacy-picture {
  padding: 3px 0 0;
}

.preview__social-pharmacy {
  width: 108.8px;
  height: 32px;
}

.preview__south-pharmacy-picture {
  padding: 6px 0 0;
}

.preview__south-pharmacy {
  width: 149.33px;
  height: 17.33px;
  object-fit: contain;
}

.how-to-get-discount {
  width: 100%;
  padding: 33px 20px 39px;
}

.how-to-get-discount__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 22px;
  line-height: 32px;
  font-weight: 700;
  margin: 0 0 14px;
}

.how-to-get-discount__description {
  margin: 0 0 23px;
}

.how-to-get-discount__discount-container {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.how-to-get-discount__discount-in-weekday {
  margin: 0 35.4px 0 0;
}

.how-to-get-discount__discount-percent {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 20px;
  line-height: 22px;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
  background: #50B848;
  border-radius: 4px;
  padding: 4px 5px 5px 7px;
  margin: 0 10px 0 0;
}

.how-to-get-qr {
  padding: 40px 20px 36.5px;
}

.how-to-get-qr__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 22px;
  line-height: 32px;
  font-weight: 700;
  margin: 0 0 14px;
}

.how-to-get-qr__description {
  margin: 0 0 20px;
}

.how-to-get-qr__list {
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  gap: 18px 0;
  counter-reset: item-counter;
  padding: 0;
  margin: 0 0 63px;
}

.how-to-get-qr__item {
  width: 96%;
  position: relative;
  padding: 0 0 0 52px;
}
.how-to-get-qr__item::before {
  counter-increment: item-counter;
  content: counter(item-counter);
  width: 36px;
  height: 36px;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #50B848;
  border-radius: 50%;
  padding: 8px 0 0;
}

.how-to-get-qr__qr {
  width: 252px;
  height: 252px;
  display: block;
  margin: 0 auto;
}

.where-to-get-qr {
  padding: 36.5px 0 0;
}

.where-to-get-qr__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 22px;
  line-height: 26px;
  font-weight: 700;
  padding: 0 20px;
  margin: 0 0 16px;
}

.where-to-get-qr__description {
  width: 95%;
  padding: 0 20px;
}

.where-to-get-qr__map {
  width: 100%;
  height: 532px;
  border: none;
}

.callback-button {
  width: 88px;
  height: 88px;
  position: fixed;
  bottom: 0;
  right: 14px;
  border: none;
  background: transparent;
  padding: 0;
}